.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    color: aliceblue;
}

.time-control {
    width: 50%;
    display: flex;

    input {
        width: 100%;
        padding: 1rem 0;
        // appearance: none;
        background: transparent;
        cursor: pointer;
    }

    p {
        padding: 1rem;
    }
}

.play-control {
    width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;

    svg {
        cursor: pointer;
    }
}

input[type="range"]:focus {
    outline: none;
}

input[type="range"]::-webkit-slider-thumb {
    appearance: none;
    height: 16px;
    width: 24px;
    border-radius: 1rem;
    background: black;
}

.track {
    background-color: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    // overflow: hidden;
}

@media screen and (max-width: 768px) {
    .time-control {
        width: 90%;
    }

    .play-control {
        width: 60%;
    }
}

